 
import React, { useEffect, useState } from 'react';

import "bootstrap/dist/css/bootstrap.min.css";
import { Link } from 'react-router-dom';
// import './LandingPage.css'; // لتنسيق الصفحة
import { Card, CardBody, Col, Container, Row } from 'reactstrap';

function LandingPage() {
  const bg="/assets/images/bg3.png"
  useEffect(() => {
    localStorage.removeItem("OTP")
    localStorage.removeItem("_grecaptcha")  
    localStorage.removeItem("accessToken")
    localStorage.removeItem("company")
    localStorage.removeItem("phone")
    localStorage.removeItem("username") 
  }, []);
  const [hover, setHover] = useState(false);
  const [hover1, setHover1] = useState(false);
  const [hover2, setHover2] = useState(false);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    // إضافة المستمع لحجم النافذة
    window.addEventListener('resize', handleResize);

    // إزالة المستمع عند تدمير المكون
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []); 
  
 
  return (  
 
 
    <div className="login1 " style={{direction:"rtl", height:"100vh"}} >
      <div
      
      style={{
        width: "20%", 
         padding:0
      }}
    >
      <img
        src="/assets/images/masterLogo.png"
        alt="الشعار"
        style={{
          height:  windowWidth  <= 768?"10%":"5%",
          width: windowWidth  <= 768 ?"90%": "75%",
          padding: windowWidth  <= 768 ?"3% 0 0 0":"4% 4% 0 0",
          borderRadius: "10px",
        }}
      />
    </div>
  {/* <div
    className="login-wrapper-background"
    style={{
      backgroundImage: `url(${bg})`,
      height: "70vh", 
      width:"100vw",
      backgroundRepeat: "no-repeat",
      backgroundSize: "contain",
      backgroundPosition: "top",
      backgroundColor: "white",
    }}>
      </div> */}
     
     <div
  className="login-wrapper-background"
  style={{
    backgroundImage: `url(${bg})`,
    width: "100vw",  // عرض 100% من الشاشة
    height: "40vh",  // أقصى ارتفاع 40% من الشاشة
    // maxHeight: "40vh", // ضمان عدم تجاوز 40vh
    backgroundRepeat: "no-repeat",
    backgroundSize: " 100% 100%",// جعل العرض 100% والطول يتمدد تلقائيًا
    backgroundPosition: "top",
    backgroundColor: "white",
  }}
></div>

      <div className="login-wrapper"
      >
        <div className="login-body">
        <h1
      style={{
        marginBottom: "10px",
        color: "#1C5633",
        fontWeight: "bold",
        wordSpacing: "5px",
        fontSize:  windowWidth  <= 768 ?"20px":"35px",
        // margin:"3%",
        textAlign: "center",
      }}
    >
     منصة نظام الحج   
    </h1>
    <p
      style={{
        color: "#BC955B",
        marginTop:  windowWidth  <= 768 ?"15px":"20px",
        fontSize:  windowWidth  <= 768 ?"15px":"20px",
        textAlign: "center",
        fontWeight: "bold",
        marginRight:"3%",
      }}
    >
      الرجاء تحديد الشركة      
    </p>
    <Row className="justify-content-center">
  {/* البطاقة الأولى */}
  <Col xs={12} md={6} className="d-flex justify-content-md-end justify-content-center ">
    <Card
      className="text-center"
      style={{
        backgroundColor: hover1 ? "#005D92" : "white",
        borderRadius: "20px",
        cursor: "pointer",
        width: "50%", // تقليل العرض في الشاشات الصغيرة
      }}
      onMouseEnter={() => setHover1(true)}
      onMouseLeave={() => setHover1(false)}
      onClick={() => {
        localStorage.setItem("company", 2); 
        window.location.href = "/Login";
      }}
    >
      <CardBody>
        <img
          className="im2"
          id="com2"
          src={hover1 ? "/assets/images/cavergod.png" : "/assets/images/logo2.png"}
          alt="الشركة 2"
          style={{
            maxHeight: "50px",
            width: "50%",
            borderRadius: "5px",
          }}
        />
      </CardBody>
    </Card>
  </Col>

  {/* البطاقة الثانية */}
  <Col xs={12} md={6} className="d-flex justify-content-md-start justify-content-center">
    <Card
      className="text-center"
      style={{
        backgroundColor: hover ? "#008086" : "white",
        borderRadius: "20px",
        cursor: "pointer",
        width: "50%", // تقليل العرض في الشاشات الصغيرة
      }}
      onMouseEnter={() => setHover(true)}
      onMouseLeave={() => setHover(false)}
      onClick={() => {
        localStorage.setItem("company", 1);
        window.location.href = "/Login";
      }}
    >
      <CardBody>
        <img
          id="com1"
          src={hover ? "/assets/images/caverkhar.png" : "/assets/images/logo1.png"}
          alt="الشركة 1"
          style={{
            maxHeight: "50px",
            width: "50%",
            borderRadius: "5px",
          }}
        />
      </CardBody>
    </Card>
  </Col>
</Row>
<div style={{ 
   width: "50%", 
    padding: "0" 
}}>
   <img
        src="/assets/images/sada-logo.png"
         style={{
          position: "absolute",  // وضع الصورة بشكل مطلق داخل الحاوية
          right: "50px",  // نقل الصورة إلى أقصى اليمين
          bottom: "20px", // نقل الصورة إلى الأسفل
          height:   "5%",
          width:   "15%",
          padding: "0",
          borderRadius: "10px",
        }}
   />
</div>
 </div>
 </div>
 </div>
 

 
    

  );
}

export default LandingPage;
